import React, {Component} from 'react';
import app from '../../../base';
import './ImageUpload.css';
import Spinner from '../../Spinner';

class ImageUpload extends Component {
  state = {
    image: null,
    url: '',
    progress: 0,
    loading: true
  }

  componentDidMount(){
    this.getPhotoUrl();
  }

  handleUpload = (e) => {
    let image;
    if (e.target.files[0]) {
      image = e.target.files[0];
    }
      if(!image) return null;
      const uploadTask = app.storage().ref(`images/user_${app.auth().currentUser.uid}`).put(image);
      uploadTask.on('state_changed', 
      (snapshot) => {
        const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
        this.setState({progress});
      }, 
      (error) => {
        console.log(error);
      }, 
    () => {
        app.storage().ref('images').child(`user_${app.auth().currentUser.uid}`).getDownloadURL().then(url => {
            this.setState({url});
        })
    });
  }

  getPhotoUrl = async () => {
    const currentUser = app.auth().currentUser;
    const photoRef = app.storage().ref('images').child(`user_${currentUser.uid}`);
    let onResolve = (url) => {
      this.setState({url, loading: false});
    }
    let onReject = () => {
      if(currentUser.photoURL){
        this.setState({url: currentUser.photoURL, loading:false});
      }
      else this.setState({loading:false})
    }
    photoRef.getDownloadURL().then(onResolve, onReject);
  }

  render() {
    const {url, progress, loading} = this.state;
    if(loading) return <Spinner/>
    return (
      <div className="user-photo">
        <h3>Profile Image</h3>
        <img src={url || 'https://rtfm.co.ua/wp-content/plugins/all-in-one-seo-pack/images/default-user-image.png'} alt="Uploaded images"/>
        <progress value={progress} max="100"/>
        <div className="custom-file" onChange={this.handleUpload}>
          <input type="file" className="custom-file-input" id="customFile" accept="image/*,image/jpeg"/>
          <label className="custom-file-label" htmlFor="customFile">Choose Your Profile Image</label>
        </div>
      </div>
    )
  }
}

export default ImageUpload;